
/**
	 * vuex管理登陆状态，具体可以参考官方登陆模板示例
	 */
import {
  mapMutations,
} from 'vuex';

export default {
  methods: {
    ...mapMutations(['login']),
  },
  onLaunch() {
    const userInfo = uni.getStorageSync('userInfo') || '';
    if (userInfo.id) {
      // 更新登陆状态
      uni.getStorage({
        key: 'userInfo',
        success: (res) => {
          this.login(res.data);
        },
      });
    }
  },
};
